"use client"
import {motion} from "framer-motion";
import Link from "next/link";
import {usePathname} from "next/navigation";
import {useState} from "react";

export default function Services(){
    const pathname = usePathname();
    const delay = pathname === "/" ? 4 : 0;
    const [isHovered, setIsHovered] = useState(false);

    const letters = ['s', 'e', 'r', 'v', 'i', 'c', 'e', 's'];

    return (
        <Link href="/services">
            <div
                className="text-l m:text-l lg:text-2xl text-[#EAEAEC] absolute left-0 top-1/4 transform -translate-y-1/2 flex flex-col items-center group pl-5"
                onMouseEnter={() => setIsHovered(true)}
                onMouseLeave={() => setIsHovered(false)}
            >
                {letters.map((letter, index) => (
                    <motion.span
                        key={index}
                        initial={{x: -50, opacity: 0}}
                        animate={{
                            x: isHovered ? 8 : 0,
                            opacity: 1,
                            color: isHovered ? '#CEABC1' : '#EAEAEC'
                        }}
                        transition={{
                            // Initial animation uses staggered delay
                            x: {duration: isHovered ? 0.1 : 1, ease: "easeInOut", delay: isHovered ? 0 : delay + (index * 0.1)},
                            opacity: {duration: 1, ease: "easeInOut", delay: delay + (index * 0.1)},
                            color: {duration: 0.1, ease: "easeInOut"}
                        }}
                    >
                        {letter}
                    </motion.span>
                ))}
            </div>
        </Link>
    );
}